	import paging from '@/components/yang-paging/fy.vue'
	import popup from '@/components/basis/popup.vue';
export default {
	components:{
		paging,
		popup
	},
	data() {
		return {
			showSpecifications:false,
			currentTab: 0,
			mensname:{
				name: '查询审核订单',
				id: 2,
				sortt:0
			},
			menslist: [
				{
					name: '功能',
					image1: '../../static/icon/meus1.png',
					image2: '../../static/icon/right.png',
					image3: '../../static/icon/lower.png',
					show: true,
					list: [
						{
							name: '查询审核订单',
							id: 2,
							sortt:1,
						},
						{
							name: '查询所有订单',
							id: 3,
							sortt:2,
						}
					]
				}
			],
			isChecked: false,
			checkedArr: [], //复选框选中的值
			allChecked: false ,//是否全选
			
			statusUserlist:[
				{
					tit:'总代理',
					status:0
				},
				{
					tit:'省代理',
					status:1
				},
				{
					tit:'市代理',
					status:2
				}
			],
			statuslist:[
				{
					tit:'未发货',
					status:0
				},
				{
					tit:'已发货',
					status:1
				},
				{
					tit:'已收货',
					status:2
				}
			],
			status:1,//已发货状态
			Selected:null,
			userlist: '',//用户信息
			menuLists:null,//信息组
			querySearch:null,//查询的值
			navigatepageNums:null,
			pageNum:1,//第几页
			pages:0,//总页数
			pageSize:0,//每页数
			total:0,//数据总条数
			wuliuNo:'',//物流号
			inputshow:0,
			showSpecifications:false,
			listdazhi:{
				receiverName:'',
				receiverMobile:'',
				receiverAddress:'',
				receiverZip:''
			}
		};
	},
	onLoad() {
		// this.judgeOrder(this.status);
	},
	onShow() {
		this.userlist = uni.getStorageSync('userlist'); //加载用户缓存
		console.log(this.userlist);
		if (this.userlist=='') {
			this.utils.error('账号未登录！', () => {
				// this.doUrl('/pages/index/index');
				// this.utils.navback();
				this.doUrl('/pages/user/login');
			});
		}
	},
	methods: {
		// 弹窗
		SpecificationsShow() {
			this.showSpecifications = true;
		},
		
		SpecificationsClose() {
			this.showSpecifications = false;
			this.listdazhi.receiverName = '';
			this.listdazhi.receiverMobile = '';
			this.listdazhi.receiverAddress = '';
			this.listdazhi.receiverZip = '';
		},
		//页码渲染
		dd(index) {
			console.log(index);
			this.pageNum = index;
			this.judgeOrder(this.status);
		},
		pagesclick(e){
			if (e) {
				this.pages = e;
				console.log(this.pages);
			} else{
				return false;
			}
		},
		userstatus(e){
			//点击其中一个选项
			if (this.status == e) {
				return false;
			} else {
				this.status = e;
				this.userlistdata(e);
			}
		},
		swichstatus(e){
			//点击其中一个选项
			if (this.status == e) {
				return false;
			} else {
				this.status = e;
				this.judgeOrder(e);
			}
		},
		// 查询接口
		getCompanyOrder(){
			if (this.querySearch!=null) {
				this.utils.showloading();
				if (this.mensname.id==2) {
					this.http.getApi('/Companyorder/getCompanyOrder', {orderNo:this.querySearch}, 'get', this.userlist.storeOpendid).then(res => {
						// console.log(res);
						uni.hideLoading();
						if (res.data.length<0) {
							this.menuLists = res.data;
							this.navigatepageNums = res.data.navigatepageNums;
							this.total = res.data.total;
							this.pageSize = res.data.pageSize;
						} else{
							this.menuLists = [res.data];
							this.status = 1;
							console.log(this.menuLists);
							this.navigatepageNums = 1;
							this.total = 1;
							this.pageSize = this.menuLists.length;
						}
					})
					.catch(err => {
						console.log(err);
						uni.hideLoading();
					});
					
				} else{
					console.log(this.mensname.id);
					this.http.getApi('/order/getOneOrder', {orderONo:this.querySearch}, 'get', this.userlist.storeOpendid).then(res => {
							console.log(res);
							uni.hideLoading();
							if (res.data.length<0) {
								this.menuLists = res.data;
								this.navigatepageNums = res.data.navigatepageNums;
								this.total = res.data.total;
								this.pageSize = res.data.pageSize;
							} else{
								this.menuLists = [res.data];
								this.status = 1;
								console.log(this.menuLists);
								this.navigatepageNums = 1;
								this.total = 1;
								this.pageSize = this.menuLists.length;
							}
						})
						.catch(err => {
							console.log(err);
							uni.hideLoading();
						});
				}
					
			} else{
				this.utils.alert('请输入查询的参数',()=>{
					return;
				});
			}
		},
		userlistdata(e){
			if (this.mensname.id == 1) {
				
				if (this.pageNum==0) {
					this.pageNum = 1;
						this.utils.showloading();
						this.http.getApi('/user/getAllUserNo', {pageNum:this.pageNum, pageSize:10}, 'get', this.userlist.storeOpendid).then(res => {
							
							uni.hideLoading();
							if (res.data.list.length==0) {
								// this.menuLists = null;
							} else{
								console.log('------------------------------');
								console.log(this.mensname.id);
								console.log(res);
								this.menuLists = res.data.list;
								console.log(this.menuLists);
								this.pageNum = res.data.pageNum;
								this.navigatepageNums = res.data.navigatepageNums;
								this.total = res.data.total;
								this.pageSize = res.data.pageSize;
							}
						})
						.catch(err => {
							console.log(err);
							uni.hideLoading();
						});
				} else{
					this.utils.showloading();
					this.http.getApi('/user/getAllUserNo', {pageNum:this.pageNum, pageSize:10}, 'get', this.userlist.storeOpendid).then(res => {
						
						uni.hideLoading();
						if (res.data.list.length==0) {
							// this.menuLists = null;
						} else{
							console.log('------------------------------');
							console.log(this.mensname.id);
							console.log(res);
							this.menuLists = res.data.list;
							console.log(this.menuLists);
							this.pageNum = res.data.pageNum;
							this.navigatepageNums = res.data.navigatepageNums;
							this.total = res.data.total;
							this.pageSize = res.data.pageSize;
						}
					})
					.catch(err => {
						console.log(err);
						uni.hideLoading();
					});
				}
				
				
			} 
		},
		// 获取全部订单
		judgeOrder(e) {
			
			if (this.mensname.id == 2) {
				this.utils.showloading();
					this.http.getApi('/Companyorder/CompanyOrder', { status: e,pageNum:this.pageNum, pageSize:10}, 'get', this.userlist.storeOpendid)
					.then(res => {
						uni.hideLoading();
						if (res.data.list.length==0) {
							this.menuLists = null;
						} else{
							console.log(res);
							this.menuLists = res.data.list;
							this.pageNum = res.data.pageNum;
							this.navigatepageNums = res.data.navigatepageNums;
							this.total = res.data.total;
							this.pageSize = res.data.pageSize;
						}
					})
					.catch(err => {
						console.log(err);
						uni.hideLoading();
					});
				
			} 
			if(this.mensname.id == 3){
				this.utils.showloading();
					this.http.getApi('/order/getorder', {myId: 0,pageNum:this.pageNum, pageSize:10}, 'get', this.userlist.storeOpendid)
					.then(res => {
						uni.hideLoading();
						// console.log(res);
						if (res.data.list.length==0) {
							this.menuLists = null;
						} else{
							console.log(res);
							this.menuLists = res.data.list;
							this.navigatepageNums = res.data.navigatepageNums;
							this.pageNum = res.data.pageNum;
							this.total = res.data.total;
							this.pageSize = res.data.pageSize;
						}
					})
					.catch(err => {
						console.log(err);
						uni.hideLoading();
					});
			}
			
			
		},
		deliverdizhi(item){
			if (item) {
				this.utils.showloading();
				this.http.getApi('/shippings/GetShipping', {ShippingID:item}, 'get', this.userlist.storeOpendid)
				.then(res => {
					uni.hideLoading();
					console.log(res);
					this.listdazhi.receiverName = res.data.receiverName;
					this.listdazhi.receiverMobile = res.data.receiverMobile;
					this.listdazhi.receiverAddress = res.data.receiverProvince + res.data.receiverCity + res.data.receiverDistrict + res.data.receiverAddress;
					this.listdazhi.receiverZip = res.data.receiverZip;
					// this.utils.success('查询成功！');
					this.SpecificationsShow();
				})
				.catch(err => {
					console.log(err);
					this.utils.success(err.msg);
					uni.hideLoading();
				});
			} else{
				
			}
			
		},
		// 发货接口
		deliverGoods(orderNo){
			if (this.inputshow!=orderNo) {
				this.inputshow = orderNo;
				this.utils.alert('请输入物流单号',()=>{
					// _this.utils.navback();
				});
			} else{
				if (this.mensname.id==2) {
					if(this.wuliuNo==''){
					this.wuliuNo = '暂无物流号';
					this.utils.showloading();
					this.http.getApi('/Companyorder/delivery', { orderNo: orderNo,wuliuNo:this.wuliuNo}, 'get', this.userlist.storeOpendid)
					.then(res => {
						uni.hideLoading();
						console.log(res);
						this.wuliuNo = '';
						this.utils.success('发货成功！');
						this.judgeOrder(this.status);
						
					})
					.catch(err => {
						console.log(err);
						this.utils.success(err.msg);
						uni.hideLoading();
					});
					
				}else{
					this.utils.showloading();
					this.http.getApi('/Companyorder/delivery', { orderNo: orderNo,wuliuNo:this.wuliuNo}, 'get', this.userlist.storeOpendid)
					.then(res => {
						uni.hideLoading();
						console.log(res);
						this.wuliuNo = '';
						this.utils.success('发货成功！');
						this.judgeOrder(this.status);
					})
					.catch(err => {
						console.log(err);
						
						uni.hideLoading();
					});
				}
				
				}
				if(this.mensname.id==3){
					if(this.wuliuNo==''){
						this.wuliuNo = '暂无物流号';
						this.utils.showloading();
						this.http.getApi('/order/delivery', { orderNo: orderNo,wuliuNo:this.wuliuNo}, 'get', this.userlist.storeOpendid)
						.then(res => {
							uni.hideLoading();
							console.log(res);
							this.wuliuNo = '';
							this.judgeOrder(this.status);
							
						})
						.catch(err => {
							console.log(err);
							uni.hideLoading();
						});
						
					}else{
						this.utils.showloading();
						this.http.getApi('/order/delivery', { orderNo: orderNo,wuliuNo:this.wuliuNo}, 'get', this.userlist.storeOpendid)
						.then(res => {
							uni.hideLoading();
							console.log(res);
							this.wuliuNo = '';
							this.judgeOrder(this.status);
						})
						.catch(err => {
							console.log(err);
							uni.hideLoading();
						});
					}
				}
					
			
			}
			
		},
		//退出登录
		loginout(){
			uni.removeStorageSync('userlist');
			this.userlist = '';
			this.utils.success('退出成功！', () => {
				this.doUrl('/pages/user/login');
				// this.utils.navback();
			});
		},
		
		menslistck(item) {
			if (item.show == false) {
				item.show = true;
			} else {
				item.show = false;
			}
		},
		swichMenu(current) {
			//点击其中一个选项
			if (this.currentTab == current.sortt) {
				this.mensname = current;
				this.pageNum = 1;
				this.judgeOrder(this.status);
				return false;
			} else {
				this.currentTab = current.sortt;
				this.mensname = current;
				this.pageNum = 1;
				this.judgeOrder(this.status);
			}
		},
		// 单个复选框事件
		checkboxChange(e) {
			let values = e.detail.orderNo;
			if (values[0] == 1) {
				this.isChecked = true;
			} else {
				this.isChecked = false;
			}
		},
		// 多选复选框改变事件
		changeCheckbox(e) {
			this.checkedArr = e.detail.value;
			// console.log(e)
			// 如果选择的数组中有值，并且长度等于列表的长度，就是全选
			if (this.checkedArr.length > 0 && this.checkedArr.length == this.menuLists.length) {
				this.allChecked = true;
			} else {
				this.allChecked = false;
			}
		},
		// 全选事件
		allChooseid(e) {
			let chooseItem = e.detail.value;
			// 全选
			if (chooseItem[0] == 'all') {
				this.allChecked = true;
				for (let item of this.menuLists) {
					let itemVal = String(item.id);
					if (!this.checkedArr.includes(itemVal)) {
						this.checkedArr.push(itemVal);
					}
				}
			} else {
				// 取消全选
				this.allChecked = false;
				this.checkedArr = [];
			}
		},
		// 全选事件
		allChoose(e) {
			let chooseItem = e.detail.value;
			// 全选
			if (chooseItem[0] == 'all') {
				this.allChecked = true;
				for (let item of this.menuLists) {
					let itemVal = String(item.orderNo);
					if (!this.checkedArr.includes(itemVal)) {
						this.checkedArr.push(itemVal);
					}
				}
			} else {
				// 取消全选
				this.allChecked = false;
				this.checkedArr = [];
			}
		}
	}
};
